            orderFlag = 'desc';
          } else if (column.order === "ascending") {
            orderFlag = 'asc';
          }
          console.info("" + column.prop + "," + column.order);

          this.sortOrderCol = "vci." + column.prop + " " + orderFlag;
          console.info("" + this.sortOrderCol);
          this.queryValveCurrentData();
        } else {
          this.sortOrderCol = '';
