          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            this.franchised_merchant_dialog = false;
          } else {
            console.info("");
            // loading.close();
            if (res.data.msg === '') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          // loading.close();
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log(":" + JSON.stringify(res));
          // loading.close();
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(res.message), '', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        });
      },
      //
      franchised_merchant_select() {
        let that = this;
        let _jsondata_ = {
          perPageSize: 100,
          currentPage: 1,
          obj_station_id: that.bank_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Franchise_scope_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            that.franchised_merchant_data = [];
            loading.close();
            that.franchised_merchant_data = res.data.rows;
            console.log(':' + JSON.stringify(that.franchised_merchant_data));
            that.toggleSelection(that.franchised_merchant_data);

          } else {
            console.info("");
            loading.close();
            if (res.data.msg === '') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(error.message), '!', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log(":" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//dialog
          this.$alert(JSON.stringify(res.message), '', {
            confirmButtonText: '',
            callback: action => {

            }
          });
        });
      },
    }
  }
</script>

